import React, { useEffect, useState } from "react";
import { fabric } from "fabric";

const Draw = () => {

    const [canvas, setCanvas] = useState('');

    useEffect(() => {
        setCanvas(initCanvas());
    }, []);

    const initCanvas = () => (
        new fabric.Canvas('canvas', {
            height: 500,
            width: 800,
            backgroundColor: '#ebebe8',
        })
    )

    const addRect = canvi => {
        const rect = new fabric.Rect({
            height: 280,
            width: 200,
            fill: '#89cdd3',
            stroke: "black"
        });
        canvi.add(rect);
        canvi.renderAll();
    }

    const save = (canvi) => {
        fetch("/save-image", {
            method: "POST",
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ image: canvi.toDataURL() }),
            mode: "cors",
        }).then(res => {
            if (res.status !== 200) {
                alert("Something went wrong")
            }
        });
    }

    return (
        <div id="draw">
            <button onClick={() => addRect(canvas)}>Add Rectangle</button>
            <button onClick={() => save(canvas)}>Save Image</button> <br />
            <canvas id="canvas" />
        </div>
    )
}

export default Draw;