import React, { useState } from "react";

const Images = () => {

    const [images, setImages] = useState([]);

    const fetchImages = () => {
        fetch("/get-images")
            .then(res => res.json())
            .then(data => setImages(data));
    }

    return (
        <div id="images">
            <button onClick={fetchImages}>Get Images</button> <br />

            {
                images.map(img => {
                    return <img src={img.image} key={img.id} alt={img.id}/> // consider storing image desc for alt
                })
            }
        </div>
    )
}

export default Images;